"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { getSubject, getChapter, getQuizzes } from "@/lib/firestore"
import type { Subject, Chapter, Quiz } from "@/types"

export default function ChapterQuiz({ 
  params 
}: { 
  params: { subject: string; chapterId: string }
}) {
  const [subject, setSubject] = useState<Subject | null>(null)
  const [chapter, setChapter] = useState<Chapter | null>(null)
  const [quizzes, setQuizzes] = useState<Quiz[]>([])
  const [currentQuizIndex, setCurrentQuizIndex] = useState(0)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const [score, setScore] = useState(0)
  const [showResult, setShowResult] = useState(false)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [subjectData, chapterData, quizzesData] = await Promise.all([
          getSubject(params.subject),
          getChapter(params.chapterId),
          getQuizzes(params.chapterId)
        ])

        if (!subjectData || !chapterData) {
          setError("Content not found")
          return
        }

        setSubject(subjectData)
        setChapter(chapterData)
        setQuizzes(quizzesData)
      } catch (error) {
        console.error("Error fetching data:", error)
        setError("Failed to load content")
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [params.subject, params.chapterId])

  const currentQuiz = quizzes[currentQuizIndex]

  const handleAnswer = (answerIndex: number) => {
    if (selectedAnswer !== null) return // Prevent multiple answers
    setSelectedAnswer(answerIndex)

    if (answerIndex === currentQuiz.correctAnswer) {
      setScore(prev => prev + currentQuiz.points)
    }

    // Move to next question after 1.5s
    setTimeout(() => {
      if (currentQuizIndex < quizzes.length - 1) {
        setCurrentQuizIndex(prev => prev + 1)
        setSelectedAnswer(null)
      } else {
        setShowResult(true)
      }
    }, 1500)
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    )
  }

  if (error || !subject || !chapter) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <p className="text-red-500">{error}</p>
          <Button onClick={() => window.location.reload()} className="mt-4">
            Retry
          </Button>
        </Card>
      </div>
    )
  }

  if (quizzes.length === 0) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <h1 className="text-2xl font-bold text-gray-800 mb-4">No Quizzes Available</h1>
          <p className="text-gray-600 mb-4">This chapter doesn't have any quizzes yet.</p>
          <Link href={`/against-one-patra/${params.subject}`}>
            <Button className="bg-orange-500">Back to Chapters</Button>
          </Link>
        </Card>
      </div>
    )
  }

  if (showResult) {
    return (
      <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white">
        <div className="container mx-auto px-6 py-8 max-w-[800px]">
          <Card className="p-8 text-center">
            <h1 className="text-2xl font-bold text-gray-800 mb-4">Quiz Complete!</h1>
            <p className="text-4xl font-bold text-orange-500 mb-6">
              Score: {score} / {quizzes.reduce((total, quiz) => total + quiz.points, 0)}
            </p>
            <div className="flex justify-center gap-4">
              <Link href={`/against-one-patra/${params.subject}`}>
                <Button className="bg-orange-500">Back to Chapters</Button>
              </Link>
              <Button 
                onClick={() => {
                  setCurrentQuizIndex(0)
                  setSelectedAnswer(null)
                  setScore(0)
                  setShowResult(false)
                }}
                className="bg-green-600"
              >
                Try Again
              </Button>
            </div>
          </Card>
        </div>
      </main>
    )
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white">
      <div className="container mx-auto px-6 py-8 max-w-[800px]">
        {/* Header */}
        <div className="mb-8">
          <Link 
            href={`/against-one-patra/${params.subject}`}
            className="text-orange-500 hover:text-orange-600 mb-2 inline-flex items-center gap-2 font-medium"
          >
            ← Back to Chapters
          </Link>
          <h1 className="text-2xl font-bold text-gray-800 mt-4">{chapter.title}</h1>
          <p className="text-gray-600 mt-1">
            Question {currentQuizIndex + 1} of {quizzes.length}
          </p>
        </div>

        {/* Quiz Card */}
        <Card className="p-8">
          <h2 className="text-xl font-semibold text-gray-800 mb-6">
            {currentQuiz.question}
          </h2>
          <div className="space-y-4">
            {currentQuiz.options.map((option, index) => (
              <button
                key={index}
                onClick={() => handleAnswer(index)}
                disabled={selectedAnswer !== null}
                className={`w-full p-4 rounded-lg text-left transition-all ${
                  selectedAnswer === null
                    ? 'hover:bg-orange-50 bg-white border border-gray-200'
                    : selectedAnswer === index
                      ? index === currentQuiz.correctAnswer
                        ? 'bg-green-100 border-green-500 border-2'
                        : 'bg-red-100 border-red-500 border-2'
                      : index === currentQuiz.correctAnswer
                        ? 'bg-green-100 border-green-500 border-2'
                        : 'bg-white border border-gray-200'
                }`}
              >
                {option}
              </button>
            ))}
          </div>
          <div className="mt-6 flex justify-between items-center">
            <p className="text-gray-600">Points: {currentQuiz.points}</p>
            <p className="text-gray-600">Score: {score}</p>
          </div>
        </Card>
      </div>
    </main>
  )
}
