import { 
  collection, 
  doc, 
  getDoc, 
  getDocs, 
  setDoc, 
  updateDoc, 
  query, 
  where,
  orderBy,
  limit,
  Timestamp,
  addDoc,
  deleteDoc
} from 'firebase/firestore'
import { db } from './firebase'
import type { Subject, Chapter, Quiz, User, QuizAttempt, DailyQuiz, Semester } from '@/types'

// User Operations
export const createUser = async (userId: string, email: string) => {
  const userRef = doc(db, 'users', userId)
  await setDoc(userRef, {
    email,
    username: email.split('@')[0],
    points: 0,
    completedQuizzes: []
  })
}

export const getUser = async (userId: string): Promise<User | null> => {
  const userRef = doc(db, 'users', userId)
  const userSnap = await getDoc(userRef)
  if (!userSnap.exists()) return null
  return { id: userSnap.id, ...userSnap.data() } as User
}

export const updateUserPoints = async (userId: string, points: number) => {
  const userRef = doc(db, 'users', userId)
  await updateDoc(userRef, {
    points: points
  })
}

export const updateUsername = async (userId: string, username: string) => {
  const userRef = doc(db, 'users', userId)
  await updateDoc(userRef, {
    username: username
  })
}

// Semester Operations
export const getSemesters = async (): Promise<Semester[]> => {
  const semestersRef = collection(db, 'semesters')
  const semestersSnap = await getDocs(query(semestersRef, orderBy('number')))
  return semestersSnap.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester))
}

export const getSemester = async (semesterId: string): Promise<Semester | null> => {
  const semesterRef = doc(db, 'semesters', semesterId)
  const semesterSnap = await getDoc(semesterRef)
  if (!semesterSnap.exists()) return null
  return { id: semesterSnap.id, ...semesterSnap.data() } as Semester
}

export const createSemester = async (number: number) => {
  const semestersRef = collection(db, 'semesters')
  await addDoc(semestersRef, {
    number,
    subjects: []
  })
}

export const deleteSemester = async (semesterId: string) => {
  // First delete all subjects in this semester
  const subjects = await getSubjects(semesterId)
  for (const subject of subjects) {
    await deleteSubject(subject.id)
  }
  
  // Then delete the semester
  const semesterRef = doc(db, 'semesters', semesterId)
  await deleteDoc(semesterRef)
}

// Subject Operations
export const getSubjects = async (semesterId: string): Promise<Subject[]> => {
  const subjectsRef = collection(db, 'subjects')
  const subjectsSnap = await getDocs(query(subjectsRef, where('semesterId', '==', semesterId)))
  return subjectsSnap.docs.map(doc => ({ id: doc.id, ...doc.data() } as Subject))
}

export const getSubject = async (subjectId: string): Promise<Subject | null> => {
  const subjectRef = doc(db, 'subjects', subjectId)
  const subjectSnap = await getDoc(subjectRef)
  if (!subjectSnap.exists()) return null
  return { id: subjectSnap.id, ...subjectSnap.data() } as Subject
}

// Chapter Operations
export const getChapter = async (chapterId: string): Promise<Chapter | null> => {
  const chapterRef = doc(db, 'chapters', chapterId)
  const chapterSnap = await getDoc(chapterRef)
  if (!chapterSnap.exists()) return null
  return { id: chapterSnap.id, ...chapterSnap.data() } as Chapter
}

export const getChapters = async (subjectId: string): Promise<Chapter[]> => {
  const chaptersRef = collection(db, 'chapters')
  const q = query(chaptersRef, where('subjectId', '==', subjectId), orderBy('order'))
  const chaptersSnap = await getDocs(q)
  return chaptersSnap.docs.map(doc => ({ id: doc.id, ...doc.data() } as Chapter))
}

// Quiz Operations
export const getQuizzes = async (chapterId: string): Promise<Quiz[]> => {
  const quizzesRef = collection(db, 'quizzes')
  const q = query(quizzesRef, where('chapterId', '==', chapterId))
  const quizzesSnap = await getDocs(q)
  return quizzesSnap.docs.map(doc => ({ id: doc.id, ...doc.data() } as Quiz))
}

export const submitQuizAttempt = async (attempt: Omit<QuizAttempt, 'id' | 'timestamp'>) => {
  const attemptsRef = collection(db, 'quizAttempts')
  await addDoc(attemptsRef, {
    ...attempt,
    timestamp: Timestamp.now()
  })
}

// Daily Quiz Operations
export const getDailyQuiz = async (): Promise<DailyQuiz | null> => {
  const dailyQuizRef = collection(db, 'dailyQuizzes')
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  
  const q = query(
    dailyQuizRef,
    where('date', '>=', Timestamp.fromDate(today)),
    where('availableUntil', '>=', Timestamp.fromDate(new Date())),
    limit(1)
  )
  
  const quizSnap = await getDocs(q)
  if (quizSnap.empty) return null
  
  const quizDoc = quizSnap.docs[0]
  return { id: quizDoc.id, ...quizDoc.data() } as DailyQuiz
}

// Admin Operations
export const createSubject = async (subject: Omit<Subject, 'id'>) => {
  const subjectsRef = collection(db, 'subjects')
  await addDoc(subjectsRef, subject)
}

export const createChapter = async (chapter: Omit<Chapter, 'id'>) => {
  const chaptersRef = collection(db, 'chapters')
  await addDoc(chaptersRef, chapter)
}

export const createQuiz = async (quiz: Omit<Quiz, 'id'>) => {
  const quizzesRef = collection(db, 'quizzes')
  await addDoc(quizzesRef, quiz)
}

export const createDailyQuiz = async (quizzes: Quiz[]) => {
  const dailyQuizRef = collection(db, 'dailyQuizzes')
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  const tomorrow = new Date(today)
  tomorrow.setDate(tomorrow.getDate() + 1)
  tomorrow.setHours(23, 59, 59, 999)
  
  await addDoc(dailyQuizRef, {
    quizzes,
    date: Timestamp.fromDate(today),
    availableUntil: Timestamp.fromDate(tomorrow)
  })
}

export const deleteSubject = async (subjectId: string) => {
  // First delete all chapters in this subject
  const chapters = await getChapters(subjectId)
  for (const chapter of chapters) {
    await deleteChapter(chapter.id)
  }
  
  // Then delete the subject
  const subjectRef = doc(db, 'subjects', subjectId)
  await deleteDoc(subjectRef)
}

export const deleteChapter = async (chapterId: string) => {
  // First delete all quizzes in this chapter
  const quizzes = await getQuizzes(chapterId)
  for (const quiz of quizzes) {
    await deleteQuiz(quiz.id)
  }
  
  // Then delete the chapter
  const chapterRef = doc(db, 'chapters', chapterId)
  await deleteDoc(chapterRef)
}

export const deleteQuiz = async (quizId: string) => {
  const quizRef = doc(db, 'quizzes', quizId)
  await deleteDoc(quizRef)
}
